@import "https://fonts.googleapis.com/css?family=Roboto:400,500";
@import "https://maxcdn.bootstrapcdn.com/font-awesome/4.5.0/css/font-awesome.min.css";
*, :before, :after {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  color: #444;
  font-family: Roboto, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
}

.review {
  height: 100%;
  background-image: url("icecreamdesk.de8efacb.png");
  flex-flow: row;
  justify-content: center;
  align-items: flex-end;
  display: flex;
  overflow: hidden;
}

.review__nav {
  width: 12px;
  height: 12px;
  z-index: 10;
  outline-offset: -6px;
  cursor: pointer;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  border-radius: 50%;
  outline: 6px solid #ccc;
  margin: 2rem 12px;
  box-shadow: 0 0 #d9d9d9, 0 0 #84a178;
}

.review__nav:checked {
  color: #84a178;
  background-color: #84a178;
  animation: check .4s linear forwards;
}

.review__nav:checked:nth-of-type(1) ~ .review__inner {
  left: 0%;
}

.review__nav:checked:nth-of-type(2) ~ .review__inner {
  left: -100%;
}

.review__nav:checked:nth-of-type(3) ~ .review__inner {
  left: -200%;
}

.review__inner {
  width: 300%;
  height: 100%;
  flex-flow: row;
  transition: left .4s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.review__contents {
  height: 100%;
  text-align: center;
  flex-flow: column;
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  display: flex;
}

.review__image {
  color: #2196f3;
  font-size: 2.7rem;
}

.review__caption {
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 255%;
}

@media screen and (min-width: 768px) {
  .review__caption {
    font-size: 16px;
    line-height: 186%;
  }
}

.review__txt {
  text-align: center;
  color: #fcfcfc;
  background-color: #84a178;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 32px;
  margin-bottom: 30px;
  padding: 34px 39px;
  font-size: 12px;
  font-weight: 400;
  line-height: 255%;
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .review__txt {
    letter-spacing: .04em;
    font-size: 14px;
    font-weight: 500;
    line-height: 186%;
  }
}

@media screen and (min-width: 1200px) {
  .review__txt {
    letter-spacing: .04em;
    font-size: 16px;
    font-weight: 500;
    line-height: 186%;
  }
}

@keyframes check {
  50% {
    outline-color: #333;
    box-shadow: 0 0 0 12px #333, 0 0 0 36px rgba(51, 51, 51, .2);
  }

  100% {
    outline-color: #333;
    box-shadow: 0 0 #333, 0 0 rgba(51, 51, 51, 0);
  }
}

